$trs: all ease 0.3s 0s;
$trf: scale(0.95);

.section {
    width: 100%;
    height: 100%;
    position: fixed;
    transition: $trs;
    -moz-transition: $trs;
    -webkit-transition: $trs;
}
.visible {
    opacity: 1;
    z-index: 5;
}
.invisible {
    opacity: 0;
    z-index: -1;
    transform: $trf;
    -moz-transform: $trf;
    -webkit-transform: $trf;
    -ms-transform: $trf;
}
