.about-mid {
	padding: 80px 10px 40px;
	height: 100vh;
	// height: calc(100vh - 80px);
    overflow: auto;
    overflow-x: hidden;
	text-align: center;
}
.about-right {
    margin-top: 80px;
}
.title-center {
	display: block;
	width: fit-content;
	margin: auto;
}
.about-align-left {
	// word-break: break-all;
}
.about-align-center {
	text-align: center;
	word-break: keep-all;
}
.hand {
	background-image: url(../../../assets/img/waving-hand.png);
	width: 30px;
	height: 30px;
	position: absolute;
	z-index: 1;
	background-size: cover;
	animation: wave 2.5s infinite; 
	@keyframes wave {
		0% { transform: rotate( 0.0deg) }
	   10% { transform: rotate(14.0deg) } 
	   20% { transform: rotate(-8.0deg) }
	   30% { transform: rotate(14.0deg) }
	   40% { transform: rotate(-4.0deg) }
	   50% { transform: rotate(10.0deg) }
	   60% { transform: rotate( 0.0deg) } 
	  100% { transform: rotate( 0.0deg) }
	}
}

#notice {
	opacity: 0;
    position: absolute;
    background: white;
    border: 1px solid black;
    bottom: 36px;
	text-align: center;
	padding: 3px;
	p {
		margin: 0;
	}
}

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
	.about-mid {
		padding: 0 20px;
		height: initial;
   		top: initial;
	}
	.about-right {
		padding: 0 20px;
		padding-bottom: 60px;
		margin-top: 10px;
	}
}

/*저해상도 태블릿, 모바일(가로)*/
@media (min-width: 481px) and (max-width: 767px) {
	.about-mid {
		padding: 0 20px;
		height: initial;
   		top: initial;
	}
	.about-right {
		padding: 0 20px;
		padding-bottom: 60px;
		margin-top: 10px;
	}
}